body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__min-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: move;
  animation-duration: 3600s;
  animation-timing-function: linear;
  animation-iteration-count: 60;
  position: relative;
  top: calc(50% - 50vmin);
  left: auto;
}

.stopwatch__min-hand:hover {
  animation-play-state: paused;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: move;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: 60;
  position: absolute;
  top: 12%;
  left: calc(50% - .75vmin);
}

.stopwatch__seconds-hand:hover {
  animation-play-state: paused;
}

.stopwatch__center {
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: auto;
  left: auto;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up :first-child {
  animation-duration: 600s;
}

.stopwatch--speed-up :nth-child(2n) {
  animation-duration: 10s;
}

